<template><section><h1>开关 Switch</h1>
<h3>基础用法</h3>
<p>表示两种相互对立的状态间的切换，多用于触发「开/关」。</p>
<demo-block>
                            <div class="source" slot="source">
  <as-row>
    <as-switch v-model="value1"></as-switch>
    <as-switch v-model="value2"></as-switch>
  </as-row>

  
</div>
                            <h1></h1>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
  <span class="hljs-tag">&lt;<span class="hljs-name">as-row</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-switch</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-switch</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">as-row</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
      data() {
        <span class="hljs-keyword">return</span> {
          <span class="hljs-attr">value1</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-attr">value2</span>: <span class="hljs-literal">false</span>
        };
      }
    };
  </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>文字描述</h3>
<p>建议1-2个字，更长的文字请考虑Radio组件</p>
<demo-block>
                            <div class="source" slot="source">
  <as-row>
    <as-switch v-model="value1" active-text="是" inactive-text="否"></as-switch>
    <as-switch v-model="value1" active-text="男生" inactive-text="女生"></as-switch>
    <as-switch v-model="value2" active-text="ON" inactive-text="OFF"></as-switch>
    <as-switch v-model="value2" active-color="#0f0"></as-switch>
    <as-switch v-model="value2" active-color="#0f0" inactive-color="#f00"></as-switch>
  </as-row>

  
</div>
                            <h1></h1>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
  <span class="hljs-tag">&lt;<span class="hljs-name">as-row</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value1"</span> <span class="hljs-attr">active-text</span>=<span class="hljs-string">"是"</span> <span class="hljs-attr">inactive-text</span>=<span class="hljs-string">"否"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-switch</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value1"</span> <span class="hljs-attr">active-text</span>=<span class="hljs-string">"男生"</span> <span class="hljs-attr">inactive-text</span>=<span class="hljs-string">"女生"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-switch</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value2"</span> <span class="hljs-attr">active-text</span>=<span class="hljs-string">"ON"</span> <span class="hljs-attr">inactive-text</span>=<span class="hljs-string">"OFF"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-switch</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value2"</span> <span class="hljs-attr">active-color</span>=<span class="hljs-string">"#0f0"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-switch</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value2"</span> <span class="hljs-attr">active-color</span>=<span class="hljs-string">"#0f0"</span> <span class="hljs-attr">inactive-color</span>=<span class="hljs-string">"#f00"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-switch</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">as-row</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
      data() {
        <span class="hljs-keyword">return</span> {
          <span class="hljs-attr">value1</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-attr">value2</span>: <span class="hljs-literal">false</span>
        };
      }
    };
  </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>禁用状态</h3>
<demo-block>
                            <div class="source" slot="source">
  <as-row>
    <as-switch v-model="value1" disabled></as-switch>
    <as-switch v-model="value2" disabled></as-switch>
    <as-switch v-model="value1" active-text="是" inactive-text="否" disabled></as-switch>
    <as-switch v-model="value1" active-text="男生" inactive-text="女生" disabled></as-switch>
    <as-switch v-model="value2" active-text="ON" inactive-text="OFF" disabled></as-switch>
    <as-switch v-model="value2" active-color="#0f0" disabled></as-switch>
    <as-switch v-model="value2" active-color="#0f0" inactive-color="#f00" disabled></as-switch>
  </as-row>

  
</div>
                            <h1></h1>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
  <span class="hljs-tag">&lt;<span class="hljs-name">as-row</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value1"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-switch</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value2"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-switch</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value1"</span> <span class="hljs-attr">active-text</span>=<span class="hljs-string">"是"</span> <span class="hljs-attr">inactive-text</span>=<span class="hljs-string">"否"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-switch</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value1"</span> <span class="hljs-attr">active-text</span>=<span class="hljs-string">"男生"</span> <span class="hljs-attr">inactive-text</span>=<span class="hljs-string">"女生"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-switch</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value2"</span> <span class="hljs-attr">active-text</span>=<span class="hljs-string">"ON"</span> <span class="hljs-attr">inactive-text</span>=<span class="hljs-string">"OFF"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-switch</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value2"</span> <span class="hljs-attr">active-color</span>=<span class="hljs-string">"#0f0"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-switch</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value2"</span> <span class="hljs-attr">active-color</span>=<span class="hljs-string">"#0f0"</span> <span class="hljs-attr">inactive-color</span>=<span class="hljs-string">"#f00"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-switch</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">as-row</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
      data() {
        <span class="hljs-keyword">return</span> {
          <span class="hljs-attr">value1</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-attr">value2</span>: <span class="hljs-literal">false</span>
        };
      }
    };
  </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>属性说明</h2>
<h3>Switch参数</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>active-text</td>
<td>switch 打开时的文字描述</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>inactive-text</td>
<td>switch 关闭时的文字描述</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>active-value</td>
<td>switch 打开时的值</td>
<td>boolean / string / number</td>
<td>-</td>
<td>true</td>
</tr>
<tr>
<td>inactive-value</td>
<td>switch 关闭时的值</td>
<td>boolean / string / number</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>active-color</td>
<td>switch 打开时的背景色</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>inactive-color</td>
<td>switch 关闭时的背景色</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>是否可用</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3>Switch Event参数</h3>
<table>
<thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>switch 状态发生变化时的回调函数</td>
<td>新状态的值</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default {
      data() {
        return {
          value1: true,
          value2: false
        };
      }
    };
  </script>


